html{
    font-family: "ADLaM Display";
}
body {
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}

.hero{
    background-color: white;
    height: 100vh;
}

.adlam-display-regular {
    font-family: "ADLaM Display", system-ui;
    font-weight: 400;
    font-style: normal;
}

/* Menu Header */
 
header.main-header{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
    padding: 0;
    justify-content: space-between;
    align-items: center;
}

   
.header-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #fff;
    z-index: 999;
}
   
.wrap-logo-header{
    position:absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    flex-shrink: 0;
}

.logo-header img {
    width: 120px; 
    height: auto; 
}
   
.logo-header{
    font-weight: bold;
    font-size: 1.2rem;
    color: #222;
}
   
.nav-header{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    flex-grow: 1;
    padding-left: 80%;
}
   
.nav-wrap-header{
    display: flex;
    flex-basis: 50%;
}
   
ul.main-menu{
    display: flex;
    width: 100%;
    z-index: 99;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    font-size: 1.1rem;
    gap: 3px;;
}
   
ul.main-menu #nav-main-contact{
    display: flex;
    align-items: center;
    padding: .5rem 1rem;
    background-color: rgb(98, 37, 15);
    color: #fff;
    
}
   
li.menu-item{
    margin: .75rem;
    display: flex;
    margin-left: 20px;
}
   
li.menu-item a{
    padding: .5rem 1rem;
    color: #222;
}
   
li.menu-item a:hover{
    color: #FFC300;
}
   
i.toggle-menu{
    display: none;
    background-color: #fff;
    border: none;
    z-index: 9999;
}
   
i.toggle-menu img{
    width: 27px;
    z-index: 99;
}
   
input#check{
    display: none;
}


.container{
    position: relative;
    height: 100vh;
}

form{
    position: absolute;
    top: 85px;
    right: 20px; 
    width: 100%;
    max-width: 450px; 
    padding: 20px;
    background: rgba(255, 255, 255, 0.8); 
    color: rgb(114, 42, 9);
    border-radius: 8px;
    box-shadow: 0 o 10px rgba(0, 0, 0, 0.2);
}

form label {
    display: block;
    margin-bottom: 8px; 
    font-weight: bold;
}

form input[type="text"],
form input[type="email"],
form textarea {
    width: 100%;
    padding: 10px 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
}

form button {
    padding: 10px 20px;
    background-color: #ffaa00; 
    border: none;
    border-radius: 7px;
    color: rgb(114, 42, 9);
    cursor: pointer;
    width:100%;
}

form button:hover {
    background-color: #e0a700; 
}

.contact-details{
    position: absolute;
    top: 50px;
    right: 50%; 
    width: 100%;
    max-width: 480px;
    color: rgb(139, 54, 31);
    padding: 10px;
    border-radius: 5px;
    z-index: 9999;
    height: auto; 
    margin-bottom: 80px;
}


.contact-form {
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.contact-form label {
    display: block;
    margin-bottom: 5px;
}

.contact-form input[type="text"],
.contact-form input[type="email"],
.contact-form textarea {
    width: 100%;
    padding: 10px ;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.contact-form button {
    padding: 10px
}


@media (min-width:880px){
    .header-wrap{
      padding: .5rem 5rem;
    }
}
   
@media (max-width:768px){
    header{
      margin-bottom: 4rem;
    }

    .header-wrap{
        position: fixed;
        width: 100%;
        background-color: #fff;
        padding: 0;
        top: 0;
        z-index: 9999;
        height: 60px;
    }
    .wrap-logo-header{
        position: fixed;
        z-index: 999;
        top: 5%;
        left: 3%;
    }
    i.toggle-menu{
        display: flex;
    }
    ul.main-menu,#nav-main-contact{
        display: none;
        left: 0;
        margin: 0;
        padding: 0;
    }
    ul.main-menu{
        position: fixed;
        background-color: #fff;
        z-index: 10000;
    }
    label.checkbtn{
        position: fixed;
        right: 3%;
        top: 3%;
        z-index: 9999;
    }
    #check:checked ~ ul{
        display: flex;
        z-index: 9;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        height: 100vh;
        top: 0;
        font-size: 2rem;
    }

    form{
        position: absolute;
        right: 40px;
        margin-top: 100px;
        width: 90%;
        max-width: 450px; 
        top: 0;
        padding: 20px;
        background: rgba(255, 255, 255, 0.8); 
        color: rgb(114, 42, 9);
        border-radius: 8px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        transform: translateY(0);
        margin-bottom: 10px;
    }
    
    form label {
        display: block;
        margin-bottom: 8px; 
        font-weight: bold;
    }
    
    form input[type="text"],
    form input[type="email"],
    form textarea {
        width: 100%;
        padding: 10px 10px;
        margin-bottom: 15px;
        border: 1px solid #ddd;
        border-radius: 4px;
    }
    
    form button {
        padding: 10px 20px;
        background-color: #ffaa00; 
        border: none;
        border-radius: 7px;
        color: rgb(114, 42, 9);
        cursor: pointer;
        width:100%;
    }
    
    form button:hover {
        background-color: #e0a700; 
    }

    .contact-details{
        position: static;
        top: 300px;
        width: 90%;
        max-width: 480px;
        color: rgb(139, 54, 31);
        padding: 10px;
        border-radius: 5px;
        z-index: -1;
        height: auto; 
        margin: 0 auto;
        margin-bottom: 40px;
    }
}